import Link from 'next/link';
import { Parallax } from 'react-parallax';
import { Box, Flex, Image } from 'rebass';
import './homepage.css';
import Events from '../event/events';

const MCM10 = () => (
    <div>
        <div>
            <Box width={[0.8, 0.2]} ml='auto' mr='auto' mt={50}>
                <Image src="/static/mcm10.svg" />
            </Box>
            <Parallax bgImage='/static/mcm_commons.jpg' strength={400} bgImageAlt=""
                renderLayer={percentage => (
                    <div style={{
                        position: 'absolute',
                        background: '#DCDFE5',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        opacity: percentage + 0.5,
                        width: percentage * 300 + 250,
                        height: percentage * 200 + 50
                    }} />
                )}>
                <div className="sponsor-link">
                    <h1>Become a Sponsor</h1>
                </div>
            </Parallax>
        </div>
        <Events />
        <Link href='/mcm10/faqs'>
            <div className='faqs-btn'>
                <p>FAQs</p>
            </div>
        </Link>
    </div>
)

export default MCM10